<template>
  <div class="search">
    <!--搜索框-->
    <div class="search-box-wrapper">
      <search-box ref="searchBox" @query="onQueryChange"></search-box>
    </div>
    <!--快速入口-->
    <div class="shortcut-wrapper" ref="shortcutWrapper" v-show="!query">
      <scroll class="shortcut" ref="shortcut"
              :data="shortcut"
              :isBeforeScroll="isBeforeScroll"
              @beforeScroll="beforeScroll">
        <div>
          <!--热门搜索-->
          <div class="hot-key">
            <h1 class="title">热门搜索</h1>
            <ul>
              <li class="item" v-for="item in hotKey" @click="addQuery(item.k)">
                <span>{{item.k}}</span>
              </li>
            </ul>
          </div>
          <!--搜索历史-->
          <div class="search-history" v-show="searchHistory.length">
            <h1 class="title">
              <span class="text">搜索历史</span>
              <span class="clear" @click="clearHistory()">
            <i class="icon-clear"></i>
          </span>
            </h1>
            <search-list :searches="searchHistory" @select="addQuery" @delete="deleteItem"></search-list>
          </div>
        </div>
      </scroll>
    </div>
    <!--搜索结果-->
    <div class="search-result" v-show="query" ref="searchResult">
      <suggest ref="suggest" :query="query" @listScroll="blurInput" @select="saveHistory"></suggest>
    </div>
    <router-view></router-view>
    <!--顶部提示-->
    <top-tip ref="topTip">
      <div class="tip-title">
        <i class="icon-ok"></i>
        <span class="text">1首歌曲已经添加到播放列表</span>
      </div>
    </top-tip>
  </div>
</template>

<script>
  import Suggest from '../../components/suggest/suggest.vue'
  import SearchBox from '../../base/search-box/search-box.vue'
  import SearchList from '../../base/search-list/search-list.vue'
  import Scroll from '../../base/scroll/scroll.vue'
  import TopTip from '../../base/top-tip/top-tip'
  import {mapMutations,mapActions, mapGetters} from 'vuex'
  import {getHotKey} from '../../api/search.js'
  import {ERR_OK} from "../../api/config.js";
  import {playListMixin} from '../../common/js/mixin'

  export default {
    mixins:[playListMixin],
    data() {
      return {
        placeholder: '搜索歌手、歌曲',
        hotKey: [],
        query: ''
      }
    },
    computed: {
      ...mapGetters([
        'searchHistory',
        'currentIndexPlay',
        'currentSong'
      ]),
      shortcut() {
        return this.hotKey.concat(this.searchHistory)
      }
    },
    created() {
      this.isBeforeScroll=true;
      this.getHot();
    },
    methods: {
      handlePlayList(playList){
        const bottom = playList.length > 0 ? '60px' : '';
        this.$refs.shortcutWrapper.style.bottom=bottom;
        this.$refs.shortcut.refresh();
      },

      //请求热门搜索数据
      getHot() {
        getHotKey().then((res) => {
          if (res.code === ERR_OK) {
            this.hotKey = res.data.hotkey.slice(0, 10)
          }
        })
      },

      //发生请求
      addQuery(query) {
        this.$refs.searchBox.setQuery(query)
      },

      //保存搜索记录同时播放歌曲
      saveHistory(song) {
        this.saveSearchHistory(this.query);//保存搜索记录
        this.playSong(song);
      },
      playSong(song){
        if(song.id===this.currentSong.id){
          return;
        }
        this.insertSong(song);//添加歌曲
        this.setCurrentPlay(this.currentIndexPlay+1);//立即播放
        if(!this.playing){
          this.setPlayingState(true);
        }
      },

      //删除搜索记录
      deleteItem(query) {
        this.deleteSearchHistory(query)
      },

      //清空搜索历史
      clearHistory() {
        this.clearSearchHistory()
      },

      //搜索
      onQueryChange(query) {
        this.query = query;
        //接收子组件search-box派发的query事件，传入query。
        //进而通过props把query传给子组件suggest
      },

      //失去焦点
      blurInput() {
        //实现收齐键盘
        //直接调用子组件的方法
        this.$refs.searchBox.blur();
      },

      //滚动之前
      beforeScroll(){
        this.$refs.shortcut.refresh();
      },

      ...mapMutations({
        setCurrentPlay:'SET_CURRENT_PLAY',
        setPlayingState:'SET_PLAYING'
      }),
      ...mapActions([
        'saveSearchHistory',
        'deleteSearchHistory',
        'clearSearchHistory',
        'insertSong',
      ])
    },
    watch: {
      query(newQuery){
        if(!newQuery || newQuery===''){
          this.$refs.shortcut.refresh()
        }
      }
    },
    components: {
      SearchBox,
      Suggest,
      SearchList,
      Scroll,
      TopTip
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .search
    .search-box-wrapper
      margin: 20px
    .shortcut-wrapper
      position: fixed
      top: 178px
      bottom: 0
      width: 100%
      .shortcut
        height: 100%
        overflow: hidden
        .hot-key
          margin: 0 20px 20px 20px
          .title
            margin-bottom: 20px
            font-size: $font-size-medium
            color: $color-text-search
          .item
            display: inline-block
            padding: 5px 10px
            margin: 0 20px 10px 0
            border-radius: 6px
            background: $color-highlight-background
            font-size: $font-size-medium
            color: $color-text-d
        .search-history
          position: relative
          margin: 0 20px
          .title
            display: flex
            align-items: center
            height: 40px
            font-size: $font-size-medium
            color: $color-text-search
            .text
              flex: 1
            .clear
              extend-click()
              .icon-clear
                font-size: $font-size-medium
                color: $color-text-d
    .search-result
      position: fixed
      width: 100%
      top: 178px
      bottom: 0
    .tip-title
      text-align: center
      padding: 18px 0
      font-size: 0
      .icon-ok
        font-size: $font-size-medium
        color: $color-theme
        margin-right: 4px
      .text
        font-size: $font-size-medium
        color: $color-text
</style>



